.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  float: left;
  width: 100%;
  box-sizing: border-box;
}

.col-1 *, .col-2 *, .col-3 *, .col-4 *, .col-5 *, .col-6 *, .col-7 *, .col-8 *, .col-9 *, .col-10 *, .col-11 *, .col-12 * {
  width: 100%;
}

/* Responsive column widths -- desktop */

@media (min-width: 700px) {
  .col-3:first-child:last-child, .col-4:first-child:last-child, .col-9:first-child:last-child {
    width: 100%;
  }
  .col-3:nth-last-child(2):first-child, .col-3:nth-last-child(2):first-child+.c, .col-4:nth-last-child(2):first-child, .col-4:nth-last-child(2):first-child+.c, .col-9:nth-last-child(2):first-child, .col-9:nth-last-child(2):first-child+.c {
    width: 50%;
  }
  .col-1 {
    width: 8.33%;
  }
  .col-2 {
    width: 16.66%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.33%;
  }
  .col-5 {
    width: 41.66%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.33%;
  }
  .col-8 {
    width: 66.66%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33%;
  }
  .col-11 {
    width: 91.66%;
  }
  .col-12 {
    width: 100%;
  }
}

/* Responsive column widths -- mobile */

@media (max-width: 700px) {
  .col-3, .col-4 {
    width: 100%;
  }
}

.wrapper:before, .wrapper:after {
  content: ' ';
  display: table;
  clear: both;
}